.button {

    padding: 14px 16px;
    border-radius: 15px;
    background-color: #ff8181;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    text-align: center;
    
    
    display: inline-block;
    margin: 20px auto;


    
    color: #000000;
    border: 0px solid;

    text-decoration: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 17px;
    line-height: 24px;
    font-style: normal;
    font-weight: 500;
    min-width: 180px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    width: 5%;
      
  }
  
  .button:hover,
  .button:focus {
    background-color: #fdc0c0;
  }